(function() {
  var hasProp = {}.hasOwnProperty;

  module.exports = {
    canDeactivate: function(to, from, next) {
      if (this.state._dirty) {
        return next(confirm("Abandon changes?"));
      } else {
        return next();
      }
    },
    clone_obj: function(obj) {
      var clone, k, v;
      clone = {};
      for (k in obj) {
        if (!hasProp.call(obj, k)) continue;
        v = obj[k];
        clone[k] = v;
      }
      return clone;
    },
    no_empties: function(element) {
      return element.trim().length > 0;
    },
    "typeof": function(subject) {
      return Object.prototype.toString.call(subject).toLowerCase().slice(8, -1);
    }
  };

}).call(this);
